.values {
    margin:0;
    font-weight: 500;
    color: #E61E2A;;
    font-size: 15px;
}

.slider{
    margin-top: 8px;
    margin-bottom: 8px;
    width: 100%;
    height: 5px;
    background-color:#FAC800;
    
}

.slider .thumb {
    width: 11px;
    height: 11px;
    cursor: grab;
    background-color: #E61E2A;;
    border: 2px solid #E61E2A;;
    top: -1.5px;
}

/* .rating {
    display: none !important;
}*/

.star{
    cursor: pointer;
    transition: color 200ms;
} 

.star-rating {
    position: relative;
    display: inline-block; 

}
  
.star-rating input[type="radio"] {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;   
    cursor: pointer;
  }